import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import {
  createBuildings,
  updateBuildings,
  getBuildingById,
  getBuildingBases,
} from 'services/building';
import { SelectAjax } from 'components/common';
import { getQueryParamId } from 'utils/urlUtil';

@Form.create({ name: 'FormDormitoryBuildingPage' })
class FormDormitoryBuildingPage extends Component {
  state = {
    initialValue: {},
  };

  componentDidMount = () => {
    if (getQueryParamId()) {
      this.getBuildingById(getQueryParamId());
    }
  };

  getBuildingById = async id => {
    const res = await getBuildingById(id);
    const initialValue = res.data;
    this.setState({ initialValue });
  };

  saveBuilding = async values => {
    const { id } = this.state.initialValue;
    values.noOfFloor = parseInt(values.noOfFloor);
    if (id) {
      await updateBuildings(values);
    } else {
      await createBuildings(values);
    }
    message.success('Lưu thành công');
    this.props.history.goBack();
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.saveBuilding(values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} tòa nhà`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Cơ sở đào tạo" required>
              {getFieldDecorator('baseId', {
                rules: [requiredRule],
                initialValue: initialValue.baseId,
              })(<SelectAjax service={getBuildingBases} />)}
            </Form.Item>
            <Form.Item hasFeedback label="Mã" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số tầng" hasFeedback>
              {getFieldDecorator('noOfFloor', {
                initialValue: initialValue.noOfFloor,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormDormitoryBuildingPage {...props} />
  </Page>
));



